import axios from "axios";
import React, { useState, useEffect, useReducer, Component } from "react";
import { useNavigate } from "react-router-dom";
import { Table, Button } from "semantic-ui-react";
import { API_URL } from "../Constants/URL";
import Create from "./Create";
import Update from "./Update";

function Read() {
  const [apiData, setAPIData] = useState([]);
  // const navigate = useNavigate();
  const updateUser = ({ firstName, lastName, checked, id }) => {
    localStorage.setItem("id", id);
    localStorage.setItem("firstName", firstName);
    localStorage.setItem("lastName", lastName);
    localStorage.setItem("checked", checked);
    // navigate("/Put");
  };
  const deleteUser = async (id) => {
    await axios.delete(API_URL + "/" + id);
    callGetAPI();
  };
  const callGetAPI = async () => {
    const resp = await axios.get(API_URL);
    setAPIData(resp.data);
  };

  useEffect(() => {
    callGetAPI();
  }, []);

  return (
    <div>
      <div>
        <button>refreshpage</button>
      </div>
      <div>
        <Table singleline>
          <Table.Header>
            <Table.Row>
              <Table.HeaderCell> Frist Name</Table.HeaderCell>
              <Table.HeaderCell> Last Name</Table.HeaderCell>
              <Table.HeaderCell> Checked</Table.HeaderCell>
              <Table.HeaderCell> delete</Table.HeaderCell>
              <Table.HeaderCell> update</Table.HeaderCell>
            </Table.Row>
          </Table.Header>
          <Table.Body>
            {apiData.map((data) => (
              <Table.Row key={data.id}>
                <Table.Cell>{data.firstName}</Table.Cell>
                <Table.Cell>{data.lastName}</Table.Cell>
                <Table.Cell>
                  {data.checked ? "T&c okay" : "T&C Not okay"}
                </Table.Cell>
                <Table.Cell>
                  <Button onClick={() => deleteUser(data.id)}>delete</Button>
                </Table.Cell>
                <Table.Cell>
                  {" "}
                  <Button onClick={() => updateUser(data)}>update</Button>
                </Table.Cell>
              </Table.Row>
            ))}
          </Table.Body>
        </Table>
      </div>
      <br />
      <div></div>
    </div>
  );
}

export default Read;
